.show-box {
    background: $clr9;
    height: 100%;
    width: 100%;

    .loader {
        margin: 0 auto;
        width: 100px;
        position: absolute;
        top: 50%;
        translate: 50% -50%;
        bottom: 0;
        right: 50%;
        padding: 5%;

        &:before {
            content: "";
            display: block;
            padding-top: 100%;
        }
    }

    .circular {
        animation: rotate 2s linear infinite;
        height: 100%;
        transform-origin: center center;
        width: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
    }

    .path {
        stroke-dasharray: 1, 200;
        stroke-dashoffset: 0;
        animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
        stroke-linecap: round;
    }

    @keyframes rotate {
        100% {
            transform: rotate(360deg);
        }
    }

    @keyframes dash {
        0% {
            stroke-dasharray: 1, 200;
            stroke-dashoffset: 0;
        }

        50% {
            stroke-dasharray: 89, 200;
            stroke-dashoffset: -35px;
        }

        100% {
            stroke-dasharray: 89, 200;
            stroke-dashoffset: -124px;
        }
    }

    @keyframes color {

        // 100%,
        0% {
            stroke: $red;
        }

        40% {
            stroke: $blue;
        }

        66% {
            stroke: $green;
        }

        // 80%,
        90% {
            stroke: $yellow;
        }
    }
}